.Contact{
    font-family: 'Bogart', sans-serif;
    font-weight: bold;
    font-size: 2.5em;
    text-align: center;
    margin-top: 1em;
    font-style: italic;
    margin-bottom: 0;
}
.icon-container {
    display: flex;
    justify-content: space-between;
    width: 25em;
    align-items: center;
    bottom: 0;
    margin: 0 auto;
    padding: 10px; 
  }
 
  .title-content {
    display: flex;
    align-items: center; 
  }
  .webIcons {
    margin: 0 20px; 
  }
  
  .icon {
    width: 4em;  
    height: 4em;
  }
  .get-in-touch{
    position: sticky;
    text-align: center;
  }
  .get-in-touch p {
    font-family: 'Bogart', sans-serif;
    font-weight: bold;
    font-size: 2.5em;
    text-align: center;
    margin-top: 1em;
  }
  
  #Resume img {
    vertical-align: middle; 
    width: 2em;
    height: 2em; 
  }
  #Resume{
    font-family: 'Bogart', sans-serif;
    font-weight: bold;
    font-size: 1.5em;
    
    text-decoration: none;
    text-align: center;
    margin-top:0;
  }